.catalog {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: getWidthCatalog($card-margin-right, $card-width, $count);
  margin: 0 auto;
  padding: 50px 0 73px;

  &__card {
    margin-bottom: $card-margin-bottom;

    &:nth-last-child(-n + 4) {
      margin-bottom: 0;
    }
  }
}
